.banner-single-full {
  position: relative;
  z-index: 1;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: var(--dark);
    opacity: 0.7;
    z-index: -1;
  }

  .content-banner {
    height: 128px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (min-width: 768px) {
      height: 232px;
    }
  }

  .title {
    @media screen and (max-width: 767px) {
      font-size: 32px;
      line-height: 48px;
    }
  }

  .form-search {
    position: relative;
    @media screen and (min-width: 920px) {
      width: 872px;
      margin: 0 auto;
    }

    .button-search,
    .button-close {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    .button-search {
      left: 27px;
    }

    .button-close {
      right: 27px;
    }

    input {
      font-size: 16px;
      line-height: 26px;
      color: var(--gray);
      letter-spacing: -0.5px;
      border-radius: 0;
      padding: 17px 64px;
      height: auto;
      width: 100%;
      background-color: var(--white);
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
      @media screen and (min-width: 768px) {
        padding: 23px 64px;
      }
    }
  }
}

.compare-table {
  color: var(--dark);
  margin: 20px 0 40px;
  @media screen and (min-width: 768px) {
    margin: 64px 0;
  }

  .product-item {
    position: relative;
    padding: 18px 8px;
    border: 1px dashed #e9e9e9;
    letter-spacing: -0.5px;
    height: 100%;
    text-align: center;
    @media screen and (min-width: 768px) {
      padding: 14px 24px 30px;
      text-align: left;
    }

    img {
      display: block;
      margin: 0 auto;
      &.img-fluid {
        height: 310px;
        object-fit: contain;
      }
      @media screen and (min-width: 1200px) {
        max-width: 290px;
      }
    }

    .title {
      line-height: 25px;
      margin-bottom: 6px;
      @media screen and (min-width: 768px) {
        line-height: 36px;
      }
    }

    .price {
      font-size: 14px;
      @media screen and (min-width: 768px) {
        font-size: 18px;
      }
    }

    .link-red {
      font-size: 14px;
      letter-spacing: -0.005em;
      display: inline-block;
      margin-top: 15px;
      .ico__chev-right {
        margin-left: 10px;
      }
      @media screen and (min-width: 768px) {
        position: absolute;
        bottom: 30px;
        right: 24px;
        margin-top: 0;
      }
    }
  }

  .btn-close {
    position: absolute;
    top: 10px;
    right: 15px;
    border: 1px solid #e9e9e9;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
  }

  .link-add {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    z-index: 10;

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background: rgba(255, 0, 0, 0.1);
    }
  }

  .specifications-accordion {
    border: 1px solid var(--line);
  }

  .product__accordion {
    padding: 0;
    margin-bottom: 0;

    &-btn {
      color: var(--dark);
      padding: 8px 15px 12px;
      background-color: var(--l-gray2);
      border-bottom: 1px solid transparent;
      @media screen and (min-width: 768px) {
        padding: 8px 24px 12px;
      }

      .icon-show {
        transition: all 0.3s;
      }

      &[aria-expanded="false"] {
        border-color: var(--line);

        .icon-show {
          transform: rotate(180deg);
        }
      }
    }

    .collapse {
      background-color: var(--white);
      .none-border-top {
        border-top: 0px;
      }
    }

    ul {
      padding-bottom: 0;

      > li {
        grid-template-columns: 1fr;
        padding: 0;

        strong {
          padding: 7px 15px 0;
          @media screen and (min-width: 768px) {
            padding: 7px 24px 0;
          }
        }

        > ul {
          display: grid;
          grid-template-columns: 1fr 1fr;
          @media screen and (min-width: 768px) {
            grid-template-columns: 1fr 1fr 1fr;
          }

          > li {
            padding: 5px 24px 10px;
            border-right: 1px solid var(--l-gray2);
            @media screen and (max-width: 767px) {
              padding: 5px 15px 10px;
              // &:last-child {
              //     display: none;
              // }
            }

            &:last-child {
              border-right: 0;
            }
          }
        }
      }
    }
  }

  .list-product {
    &.fixed {
      position: fixed;
      background: white;
      width: calc(100% - 30px);
      max-width: calc(1312px - 30px);
      z-index: 1;

      top: 60px;

      .product-item {
        img,
        .btn-close {
          display: none;
        }
        .icon img {
          display: block;
        }
      }
      @include respond-from($lg) {
        top: 85px;
      }
    }
  }
  @include respond-to($sm) {
    .product-item {
      img.img-fluid {
        height: 130px;
      }
    }
  }
}

.preview-modal {
  .modal-dialog {
    max-width: 840px;
    @media screen and (max-width: 767px) {
      margin: 0;
    }
  }

  .modal-header {
    background-color: var(--l-gray2);
    border-radius: 0;

    .modal-title {
      font-family: $mainBold;
      font-size: 18px;
      line-height: 28px;
      font-weight: 600;
      letter-spacing: -0.2px;
      text-align: left;
    }

    .close {
      color: #000;
      @media screen and (max-width: 767px) {
        opacity: 1;
      }
    }
  }

  .modal-body {
    padding-top: 0;
    overflow: hidden;
    @media screen and (min-width: 768px) {
      padding-top: 40px;
    }
  }

  .modal-content {
    height: 100vh;
    border: 0;
    border-radius: 0;
    @media screen and (min-width: 768px) {
      height: auto;
    }
  }

  .h-menu__category {
    position: relative;
    width: 304px;
    flex-shrink: 0;
    padding-right: 30px;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: -100em;
      bottom: -100em;
      width: 1px;
      background-color: var(--l-gray2);
    }
  }

  .h-menu__content {
    width: 100%;

    .h-menu__section {
      display: none;

      &.active {
        display: block;
      }
    }
  }

  .navmenu-drawer__content {
    margin-top: 0;
  }

  .btn__h-drawer {
    padding-left: 0;
    padding-right: 0;
    box-shadow: none;
    &::after {
      content: "";
      width: 14px;
      height: 9px;
      background-size: cover;
      background-repeat: no-repeat;
      background-image: url("../img/compare/icons/icon-up.svg");
      transform: rotate(180deg);
      transition: all 0.3s;
    }
    &[aria-expanded="true"] {
      &::after {
        transform: rotate(0);
      }
    }
  }
}
